.body {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.background {
  background: #D7D8DD; /* fallback for old browsers */
  /* background-image: linear-gradient(rgba(215,216,221,0.3),rgba(215,216,221,0.3)),url("Notebook.jpg"); */
  background-image: linear-gradient(rgba(215,216,221,0.3),rgba(215,216,221,0.3)),url("Notebook1440p.jpg");
  /* background-image: linear-gradient(rgba(215,216,221,0.3),rgba(215,216,221,0.3)),url("Notebook480p.jpg"); */
  background-size: cover;
  background-position: center;
  animation: lazyLoading 1s;
}

.lazyLoading {
  background-image: linear-gradient(rgba(215,216,221,0.3),rgba(215,216,221,0.3)),url("Notebook1440p.jpg");
}

@keyframes lazyLoading{
  from {
    background-image: linear-gradient(rgba(215,216,221,0.3),rgba(215,216,221,0.3)),url("Notebook480p.jpg");
  }
  to {
    background-image: linear-gradient(rgba(215,216,221,0.3),rgba(215,216,221,0.3)),url("Notebook1440p.jpg");
  }
}

